import React from "react"

function Project(props){
    return(
        <div className="project_card">
            <div className="project_card_content">
                <div className="project_card_front">
                    <a href= {props.myProject.github}>
                        <h3>{props.myProject.name}</h3>
                    </a>
                    <div className="project_info">
                        <div className="project_img_container">
                            <img src={ props.myProject.img1 } alt="Project pic"/>
                            {/*<img className="proj-img" src={ props.myProject.img2 } alt="Project pic"/>*/}
                        </div>
                    </div>
                </div>
                <div className="project_card_back">
                    {/*<a href= {props.myProject.github}>
                        <h3>{props.myProject.name}</h3>
                    </a>*/}
                    <div className="project_info">
                        <div>
                            <p>{props.myProject.info}</p>
                        </div>
                        <div className="project_links">
                            <a href={props.myProject.live}>Live</a>
                            <span>  </span>
                            <a href={props.myProject.github}>GitHub</a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Project