import React from "react"
import Project from "./Project"

/****** import images ******/


function Projects(props){
    const myProjects=[{
                name: "Schemakollen",
                info: "Android app för ditt arbetsschema. Ha koll på dina aretstider. Möjlighet att se schema per vecka, månad eller nästkommande arbetspass i en widget. Räknar ut din lön efter inställningar och val av fackligt avtal, drar av skatt efter skatteverkets skattetabell och visar slutlön. Native app skriven i Java med SQL databas",
                img1: require("./img/projects/nexus-schema1.png"),
                img2: require("./img/projects/nexus-schema2.png"),
                github: "http://github.com/Hotheda/Schemakollen",
                live: "https://play.google.com/store/apps/details?id=com.heda.schemakollen"
            },{
                name: "Fältskytte",
                info: "App för skjutledaren vid fältskytte. Visar tidpunkt för komandon och även ljud och vibration. Native app skriven i Java",
                img1: require("./img/projects/nexus-falt1.png"),
                img2: require("./img/projects/nexus-falt2.png"),
                github: "http://github.com/Hotheda/Faltskytte",
                live: "https://play.google.com/store/apps/details?id=com.heda.faltskytte"
            },{
                name: "Webshop",
                info: "Webshop skriven i React.js. API för att maila orderbekräftelse och Paypal integtation",
                img1: require("./img/projects/webshop.jpg"),
                img2: require("./img/projects/nexus-falt2.png"),
                github: "https://github.com/Hotheda/Calvinshop",
                live: "https://hotheda.github.io/calvin/"
            }
        ];

        /***** Mappar min function till 2 components *****/
        const myProj = myProjects.map((myProjects)=> <Project key={myProjects.name} myProject={myProjects} /> )

    return(
        <div className="projects" ref={props.myRef} >
            <h2 style={{textDecoration: "underline"}} >Projects</h2>
            <h3>Here are some of my previous projects</h3>
            <br/>
            <div className="my_project">
                {myProj}
            </div>
        </div>
    )
}

export default Projects