import React from "react"
import {ReactComponent as MailLogo} from "./img/at-solid.svg"

function About(props){
    return(
        <div className="fullpage">
            <div className="about" ref={props.myRef}>
                <h2>About</h2>
                <br/>
                <p>Mitt namn är Kristofer Odehammar</p>
                <p>Problemlösning är något jag verkligen brinner för</p>
                <p>och det är en av anledningarna att programmering är ett stort intresse</p>
                <br/>
                <p>Allt startade med en grundkurs i programmering i skolan,</p>
                <p>i samband med detta började jag programmera på fritiden.</p>
                <p>Därefter har jag fortsatt och jobbat med diverse olika språk.</p>
                <p>Resan började med Basic / Visual Basic med bland annat</p>
                <p>Rättstavnings och tidsloggnings program,</p>
                <p>Och fortsatte sedan i C++ med några spelprojekt.</p>
                <br/>
                <p>Fick senare upp ögonen för C# och skapade även några appar i Java för android.</p>
                <p>Jag älskar att lära mig nya saker och jobbar för tillfället med</p>
                <p>HTML / CSS / JavaScript och React.js.</p>
                <br/>
                <p>Tveka inte att höra av er om ni har några frågor eller är intresserade.</p>
                <br/>
                <a tooltip="Send me an e-mail" href="mailto:kristofer@odehammar.com">
                    <MailLogo className="logolink"/>
                </a>
        </div>
        </div>
    )
}

export default About